.modal {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  z-index: 3;
  background-color: rgba(0, 0, 0, .5);
  transition: visibility 0s linear .25s, opacity .25s, transform .25s;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1.1);
}

.modal-content {
  width: 24rem;
  background-color: #fff;
  border-radius: .5rem;
  padding: 1rem 1.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-button {
  float: right;
  width: 1.5rem;
  text-align: center;
  cursor: pointer;
  background-color: #d3d3d3;
  border-radius: .25rem;
  line-height: 1.5rem;
}

